import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartlist: []
  },
  mutations: {
    addCartList (state, payload) {
      if (localStorage.productCart) {
        console.log(payload.name)
        var arr = JSON.parse(localStorage.getItem('productCart'))
        for (let i = 0; i < arr.length; i++) {
          if (payload.name === arr[i].name) {
            arr[i].count++
            state.cartlist = arr
            break
          } else {
            state.cartlist = arr.concat(payload)
          }
        }
        // state.cartlist = arr
        localStorage.setItem('productCart', JSON.stringify(state.cartlist))
      } else {
        console.log('28')
        state.cartlist.push(payload)
        localStorage.setItem('productCart', JSON.stringify(state.cartlist))
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
